:root {
  --paper: #f7f2ea;
  --seal: #7d1f2a;
  --seal-dark: #5f1821;
  --accent: #b07d4f;
  --accent-dark: #8e633f;
}

body {
  margin: 0;
  font-family: 'Georgia', serif;
  background: var(--paper);
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 480px;
  padding: 20px;
  text-align: center;
}

.page {
  display: none;
}

.page.active {
  display: block;
  animation: fadeIn 0.6s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.seal-btn {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  background: radial-gradient(circle at 35% 35%, #9a2431 0%, var(--seal) 40%, var(--seal-dark) 100%);
  box-shadow: inset 0 8px 18px rgba(255, 255, 255, 0.18),
    inset 0 -10px 22px rgba(0, 0, 0, 0.22),
    0 12px 20px rgba(0, 0, 0, 0.18);
  border: 5px solid rgba(255, 255, 255, 0.35);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  cursor: pointer;
}

.seal-mark {
  color: #f6e6d6;
  font-size: 32px;
  font-weight: bold;
  font-family: 'Georgia', serif;
}

.btn {
  display: block;
  margin: 12px auto;
  padding: 14px 20px;
  background: var(--accent);
  color: #fff;
  text-decoration: none;
  border-radius: 8px;
  font-size: 18px;
  max-width: 90%;
  transition: background 0.3s;
}

.btn:hover {
  background: var(--accent-dark);
}

@media (min-width: 768px) {
  .container {
    max-width: 700px;
  }

  .seal-btn {
    width: 180px;
    height: 180px;
  }

  .seal-mark {
    font-size: 40px;
  }

  .btn {
    font-size: 20px;
  }
}

.letter-cover {
  width: 100%;
  height: 100vh;
  background-color: var(--paper);
  background-image: url("../public/img/letter-cover.png");
  background-size: contain;
  /* NON taglia più l’immagine */
  background-position: center top;
  /* Mantiene la geometria */
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  padding: 0;
}

/* Effetto piega della busta */
.letter-cover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: linear-gradient(to bottom,
      rgba(0, 0, 0, 0.08),
      rgba(0, 0, 0, 0.02));
  clip-path: polygon(0 0, 100% 0, 50% 100%);
  opacity: 0.25;
}

.letter-inner {
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}